import React, {useEffect, useState} from 'react';
import {useObserver} from 'mobx-react';
import {Chip, Grid, Theme} from "@material-ui/core";
import ContentLayout from "../../components/commons/ContentLayout";
import {FirebaseServices} from "../../services";
import {IDocUser} from "../../constants/interfaces";
import MaterialTable, {MTableToolbar} from "material-table";
import LoadingBackground from "../../components/commons/LoadingBackdrop";
import {makeStyles} from "@material-ui/core/styles";
import {UserTypes} from "../../enums";
import {DETAIL_USER} from "../../constants/routes";
import {useHistory} from "react-router-dom";

const useStyles = makeStyles((theme: Theme) => ({
    chips: {
        margin: '8px',
        color: 'white',
    }
}));

const ListUser: React.FC = () => {
    const classes = useStyles();
    const history = useHistory();
    const [users, setUsers] = useState<IDocUser[]>([]);
    const [loading, setLoading] = useState<boolean>(false);
    const [userCountChip, setUserCountChip] = useState<string>('Users : 0');


    useEffect(() => {
        const init = async () => {
            setLoading(true);
            const resUsers = await FirebaseServices.getInstance().fetchUsers();
            setUsers(resUsers);
            setUserCountChip(`Users : ${resUsers.filter(v => v.role === UserTypes.USER).length}`);
            setLoading(false);
        };

        init();
    }, []);

    return useObserver(() => (<ContentLayout>
        <LoadingBackground open={loading}/>
        <Grid container>
            <Grid item xs={12}>
                <div style={{maxWidth: "100%"}}>
                    <MaterialTable
                        title={'Users'}
                        columns={[
                            {title: 'Email', field: 'email'},
                            {title: 'Role', field: 'role'},
                            {title: 'Last Sign In', field: 'lastSignIn', type: 'datetime'},
                            {title: 'Created at', field: 'createdAt', type: 'datetime'},
                        ]}
                        options={{
                            grouping: true,
                            actionsColumnIndex: -1,
                        }}
                        data={users}
                        actions={[
                            {
                                icon: 'open_in_browser',
                                tooltip: 'Open applicant data',
                                // onClick: (event, rowData) => window.open(`${CURRENT_ROUTE}${DETAIL_USER}${(rowData as IDocUser).uid}`, '_blank'),
                                onClick: (event, rowData) => history.push(`${DETAIL_USER}${(rowData as IDocUser).uid}`),
                            }
                        ]}
                        components={{
                            Toolbar: props => (
                                <>
                                    <MTableToolbar {...props} />
                                    <div style={{padding: '0px 10px'}}>
                                        <Chip label={userCountChip} color={'secondary'} className={classes.chips}/>
                                    </div>
                                </>
                            )
                        }}
                    />
                </div>
            </Grid>
        </Grid>
    </ContentLayout>));
};

export default ListUser;
